import React from 'react';
import AbstractList from '../Core/AbstractList'

const ButtonStyle = {
    marginLeft: "10px"
}


export default class OrderState extends AbstractList {

	constructor( props ) {
		super( props );
		this.refreshOptions = this.refreshOptions.bind( this );
		this.getCreatePanel = this.getCreatePanel.bind( this );
	}

	refreshOptions() {
		this.rest.options()
			.then(resp => this.setState({ options: resp.data }));
	}

	getEntityName() {
		return "orderState";
	}

	getSearchPanel() {
		return [
			{ id: "EQ:title", type: 'text', label: '' }
		]
	}

	getTableHead() {
		return ["", "", " ", ""];
	}

	getCreatePanel() {
		let value = ( this.state.data ? this.state.data.parentId : undefined );
		return [
			{ id: "title", type: 'text', label: '' },
			{ id: "parent", type: "combo", label: '', value: value, values: this.state.options ? this.state.options.states : [],
				convertToVal: it => it.id , convertToStr: it => it.title }
		];
	}

	saveEntity(entity) {
		this.refineComboBox( "parent", entity );
		super.saveEntity( entity )
			.then( res => this.refreshOptions() );
	}


	convertToTableRow( data ) {
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.title}</td>
				<td>{data.parentTitle}</td>
				<td>
					<a style={ButtonStyle} href="javascript:void(0);"><i className="fa fa-pencil" aria-hidden="true" onClick={this.editEntity.bind(this, data.id)}></i></a>
				</td>
			</tr>
		);
	}

}



